html {
    font-family: 'Exo 2.0', sans-serif;
}
$black-color: #0F1110;
$blue-color: #27A0D6;
$light-black: rgba(0, 0, 0, 0.68);
//Общие стили
.wrapper{
    width: 100%;
    padding: 0 130px;
}
.wrapper-right {
    padding-left: 0;
}
.wrapper-left{
    padding-right: 0;
}
.new-block {
    padding: 80px 0;
}
.h2-title{
    font-style: normal;
    font-weight: bold;
    font-size: 42px;
    line-height: 132%;
    text-align: center;
    color: $blue-color;
}
.auto-padding{
    padding-top: 140px;
    padding-bottom: 100px;
}
.none-modal{
    display: none;
}
.-img img{
    max-width: 100%;
    display: block;
}
.btn {
    box-shadow: 5px 9px 12px 0px rgba(0,0,0, 0.4);
    background: #fff;
    padding: 15px 40px;
    color: $blue-color;
    border: none;
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 110%;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    position: relative;
    &::before{
        top: -62%;
        content: '';
        display: block;
        -webkit-transform: rotate(25deg);
        transform: rotate(25deg);
        position: absolute;
        width: 20px;
        height: 130px;
        z-index: 1;
        background: $blue-color;
        -webkit-animation: 2.5s ease-out infinite btn-effect;
        animation: 2.5s ease-out infinite btn-effect
    }
}
.btn-blue{
    background: $blue-color;
    color: #fff;
    &::before{
        background: #fff;
    }
}
h1{
    letter-spacing: 0.05em;
}
//Стили для js
.out{
    transform: translateY(-100%);
}
.has_menu{
    position: relative;
    &:hover{
        &::before{
            color: $blue-color;
        }
    }
    &::before{
        content: '';
        background: url("../img/my-arrow-down.png");
        background-size: cover;
        position: absolute;
        left: 130px;
        top: 50%;
        transform: translateY(-30%);
        color: #fff;
        width: 14px;
        height: 14px;
    }
}
@keyframes btn-effect {
    0% {
        left: -20%
    }

    100%,30% {
        left: 110%
    }
}
//first block
nav{
    padding: 20px 0;
    position: fixed;
    top: 0;
    min-height: 70px;
    background-color: #fff;
    width: 100%;
    transition: all .3s ease;;
    z-index: 15;
}
.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-logo {
        max-width: 130px;
        a{
            display: block;
            img{
                max-width: 100%;
                margin: 0 auto;
                display: block;
            }
        }
    }
    &-content, &-burger{
        ul {
            display: flex;
            li {
                a{
                    padding: 0 15px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 82.3%;
                    text-decoration: none;
                    color: $black-color;
                    transition: all 0.2s;
                }
                a.menu-active{
                    color: $blue-color;
                }
                &:hover > a{
                    color: $blue-color;

                }
                &:hover > .dop-menu {
                    display: block;
                }
            }
        }
        //    выпадающий список
        .dop-menu{
            background: #fff;
            position: absolute;
            //display: block;
            display: none;
            li {
                transition: all 0.2s;
                border-bottom: 1px solid #cccccc;
                a{
                    padding: 16px 20px;
                    display: block;
                    transition: all 0.2s;
                }
                &:last-child{
                    border: none;
                }
                &:hover{
                    background: $blue-color;
                    border-bottom: 1px solid $blue-color;
                    a {
                        color: #fff;
                    }
                }
            }
        }
    }
    &-contacts{
        p{
            margin-bottom: 10px;
            &:hover a{
                color: $blue-color;
            }
            a{
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                line-height: 82.3%;
                text-decoration: none;
                color: $black-color;
                transition: all 0.2s;
            }
        }
    }
    &-burger{
        z-index: 15;
        display: none;
        position: fixed;
        top: 0;
        width: 100%;
        left: 100%;
        min-height: 100%;
        background: #2b2b2b;
        transition: 0.3s;
        & > ul {
            justify-content: center;
            flex-direction: column;
            height: 100%;
            li {
                a{
                    padding: 20px 20px;
                    display: block;
                    transition: .2s;
                    color: #fff;
                }
                .dop-menu{
                    position: static;
                    background: #2b2b2b;
                    margin-left: 40px;
                    li{
                        border: none;
                    }
                }
            }
        }
        .close-burger{
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            width: 50px;
            height: 50px;
            align-items: center;
            justify-content: center;
            &::after{
                content: '';
                position: absolute;
                display: block;
                width: 2px;
                height: 25px;
                background: #fff;
                transform: skewX(45deg);
            }
            &::before{
                content: '';
                position: absolute;
                display: block;
                width: 2px;
                height: 25px;
                background: #fff;
                transform: skewX(-45deg);
            }
        }
    }
    &-burger-btn{
        display: none;
    }
}
