            display: block;
            img{
                display: block;
                max-width: 100%;
                margin: 0 auto;
            }
        }
    }
}

//СТРАНИЦА ПРОДУКТОВ
.products{
    background: #EEE;
    .-title{
        margin-top: 60px;
        margin-bottom: 80px;
        text-align: center;
        h1 {
            font-weight: bold;
            font-size: 40px;
            line-height: 110.2%;
        }
    }
    &-content{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-gap: 40px 20px;
        justify-items: center;
        &__elem{
            display: block;
            background: #fff;
            max-width: 320px;
            width: 100%;
            a{
                display: block;
                text-decoration: none;
                padding-bottom: 30px;
            }
            .-img{
                height: 320px;
                display: flex;
                align-items: center;
                overflow: hidden;
            }
            .-text{
                text-align: center;
                padding: 0 20px;
                margin: 0 auto;
                margin-top: 30px;
                color: #000;
                p{
                    font-weight: bold;
                    font-size: 17px;
                    line-height: 132%;
                    text-transform: uppercase;
                    letter-spacing: 0.05em;
                }
            }
        }
    }
}

//СТРАНИЦА КАРТОЧКИ ТОВАРА
.cart{
    background: #eee;
    &-content{
        margin-top: 60px;
        color: #000;
        letter-spacing: 0.05em;
        &-general{
            display: flex;
            margin-bottom: 80px;
            .-img{
                max-width: 540px;
                background: #fff;
                padding: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
            }
            .-text{
                margin-left: 80px;
            }
        }
        h1{
            margin-bottom: 45px;
            font-weight: bold;
            font-size: 42px;
            line-height: 110.2%;
        }
        strong, p{
            margin-top: 24px;
            margin-bottom: 20px;
            font-weight: bold;
            font-size: 20px;
            line-height: 110.2%;
        }
        span{
            font-weight: normal;
            font-size: 18px;
            letter-spacing: normal;
            line-height: 120%;
        }
        ul{
            margin-top: 24px;
            font-weight: bold;
            font-size: 20px;
            line-height: 110.2%;
            li{
                margin-bottom: 7px;
                margin-left: 30px;
                font-weight: normal;
                font-size: 20px;
                line-height: 110.2%;
                &:first-child{
                    margin-top: 10px;
                }
            }
        }
    }
}



