  font-size: 42px;
  line-height: 132%;
  text-align: center;
  color: #27A0D6; }

.auto-padding {
  padding-top: 140px;
  padding-bottom: 100px; }

.none-modal {
  display: none; }

.-img img {
  max-width: 100%;
  display: block; }

.btn {
  box-shadow: 5px 9px 12px 0px rgba(0, 0, 0, 0.4);
  background: #fff;
  padding: 15px 40px;
  color: #27A0D6;
  border: none;
  font-style: normal;
  font-weight: 700;
  font-size: 19px;
  line-height: 110%;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  position: relative; }
  .btn::before {
    top: -62%;
    content: '';
    display: block;
    -webkit-transform: rotate(25deg);
    transform: rotate(25deg);
    position: absolute;
    width: 20px;
    height: 130px;
    z-index: 1;
    background: #27A0D6;
    -webkit-animation: 2.5s ease-out infinite btn-effect;
    animation: 2.5s ease-out infinite btn-effect; }

.btn-blue {
  background: #27A0D6;
  color: #fff; }
  .btn-blue::before {
    background: #fff; }

h1 {
  letter-spacing: 0.05em; }

.out {
  transform: translateY(-100%); }

.has_menu {
  position: relative; }
  .has_menu:hover::before {
    color: #27A0D6; }
  .has_menu::before {
    content: '';
    background: url("../img/my-arrow-down.png");
