    position: absolute;
    left: 130px;
    top: 50%;
    transform: translateY(-30%);
    color: #fff;
    width: 14px;
    height: 14px; }

@keyframes btn-effect {
  0% {
    left: -20%; }
  100%, 30% {
    left: 110%; } }

nav {
  padding: 20px 0;
  position: fixed;
  top: 0;
  min-height: 70px;
  background-color: #fff;
  width: 100%;
  transition: all .3s ease;
  z-index: 15; }

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .nav-logo {
    max-width: 130px; }
    .nav-logo a {
      display: block; }
      .nav-logo a img {
        max-width: 100%;
        margin: 0 auto;
        display: block; }
  .nav-content ul, .nav-burger ul {
    display: flex; }
    .nav-content ul li a, .nav-burger ul li a {
      padding: 0 15px;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 82.3%;
      text-decoration: none;
      color: #0F1110;
      transition: all 0.2s; }
    .nav-content ul li a.menu-active, .nav-burger ul li a.menu-active {
      color: #27A0D6; }
    .nav-content ul li:hover > a, .nav-burger ul li:hover > a {
      color: #27A0D6; }
    .nav-content ul li:hover > .dop-menu, .nav-burger ul li:hover > .dop-menu {
      display: block; }
  .nav-content .dop-menu, .nav-burger .dop-menu {
    background: #fff;
    position: absolute;
    display: none; }
    .nav-content .dop-menu li, .nav-burger .dop-menu li {
      transition: all 0.2s;
      border-bottom: 1px solid #cccccc; }
      .nav-content .dop-menu li a, .nav-burger .dop-menu li a {
        padding: 16px 20px;
        display: block;
        transition: all 0.2s; }
      .nav-content .dop-menu li:last-child, .nav-burger .dop-menu li:last-child {
        border: none; }
      .nav-content .dop-menu li:hover, .nav-burger .dop-menu li:hover {
        background: #27A0D6;
        border-bottom: 1px solid #27A0D6; }
        .nav-content .dop-menu li:hover a, .nav-burger .dop-menu li:hover a {
          color: #fff; }
  .nav-contacts p {
    margin-bottom: 10px; }
    .nav-contacts p:hover a {
      color: #27A0D6; }
    .nav-contacts p a {
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 82.3%;
      text-decoration: none;
      color: #0F1110;
      transition: all 0.2s; }
  .nav-burger {
    z-index: 15;
    display: none;
    position: fixed;
    top: 0;
    width: 100%;
    left: 100%;
    min-height: 100%;
    background: #2b2b2b;
    transition: 0.3s; }
    .nav-burger > ul {
      justify-content: center;
      flex-direction: column;
      height: 100%; }
      .nav-burger > ul li a {
        padding: 20px 20px;
        display: block;
        transition: .2s;
        color: #fff; }
      .nav-burger > ul li .dop-menu {
        position: static;
        background: #2b2b2b;
        margin-left: 40px; }
        .nav-burger > ul li .dop-menu li {
          border: none; }
    .nav-burger .close-burger {
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      width: 50px;
      height: 50px;
      align-items: center;
      justify-content: center; }
      .nav-burger .close-burger::after {
        content: '';
        position: absolute;
        display: block;
        width: 2px;
        height: 25px;
        background: #fff;
        transform: skewX(45deg); }
      .nav-burger .close-burger::before {
        content: '';
        position: absolute;
        display: block;
        width: 2px;
        height: 25px;
        background: #fff;
        transform: skewX(-45deg); }
  .nav-burger-btn {
    display: none; }

