//@media (от 1399 до 1580)
@media (max-width: 1580px){
    .aboutus{
        &-h2-title{
            font-size: 38px;
        }
        //
        &-info-content{
            .-descr{
                padding: 35px 45px 40px 55px;
                p{
                    font-size: 16px;
                }
            }
        }
        //
        &-vipusk-content__elem{
            .-text{
                padding-left: 30px;
                p{
                    font-size: 17px;
                }
            }
        }
        //
        &-strategiya-content p{
            line-height: 140%;
        }
    //
        &-personal-content__elem .-descr{
            overflow-y: scroll;
            span{
                font-size: 18px;
                line-height: 140%;
            }
        }
    }

}



//@media (от 1024 до 1399)
@media (max-width: 1399px){
    .wrapper{
        padding: 0 70px;
    }
    .nav{
        flex-wrap: wrap;
        &-logo{
            order: 1;
        }
        &-content{
            order: 3;
            margin-top: 30px;
            width: 100%;
            display: flex;
            justify-content: center;
        }
        &-contacts{
            order: 2;
            display: flex;
            p{
                margin-right: 25px;
                &:nth-child(3){
                    margin-right: 0;
                }
            }
        }
    }

    .header-content{
        text-align: right;
        h1{
            font-size: 78px;
            margin-bottom: 25px;
        }
    }

    .why-we__content p {
        font-size: 17px;
    }
    .dvizheniye-content__elem{
        max-width: 350px;
    }
    .luchshe-content{
        grid-column-gap: 25px;
        grid-row-gap: 55px;
        &__elem{
            span{
                font-size: 16px;
                padding: 0px 15px
            }
        }
    }
    .feedback-content{
        justify-content: space-between;
        &__map{
            width: 50%;
        }
        &__form {
            padding: 45px 45px;
        }
    }
    .footer-content{
        grid-template-columns: repeat(2,1fr);
        justify-items: center;
        grid-row-gap: 30px;
        &__elem{
            display: flex;
            flex-direction: column;
            align-items: center;
            p{
                text-align: center;
            }
        }
    }
//    СТРАНИЦА О КОМПАНИИ
    .aboutus{
        &-h2-title {
            font-size: 32px;
        }
        &-header-content-wrapper .-title{
            padding-top: 140px;
        }
        &-second-block{
            padding: 40px 0;
        }
        &-info-content{
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            .-img{
                max-width: 950px;
                order: 1;
                width: 100%;
                max-height: 300px;
                overflow: hidden;
                filter: brightness(80%);
            }
            .-descr{
                order: 2;
                width: 100%;
                max-width: 950px;
            }
        }
    //
        &-vipusk-content{
            justify-content: center;
            &__elem{
                max-width: 400px;
                width: 33.3%;
                margin-bottom: 15px;
                padding-right: 15px;
                .-text{
                    padding: 0 10px 20px 20px;
                    p{
                        font-size: 15px;
                    }
                    span{
                        font-size: 15px;
                    }
                }
            }
        }
    //
        &-strategiya{
            padding: 65px 70px;
            &-content{
                p{
                    font-size: 18px;
                    margin-bottom: 15px;
                }
            }
        }
    //
        &-personal-content__elem .-descr{
            padding: 15px 10px 35px 30px;
            p{
                font-size: 22px;
            }
        }


    }

    //СТРАНИЦА КОНТАКТЫ
    .contacts{
        &-content{
            grid-template-columns: repeat(3, 1fr);
        }
    }

    //СТРАНИЦА ПРОДУКТОВ
    .products{
        &-content{
            &__elem{
                .-text{
                    padding: 0 15px;
                    p{
                        font-size: 16px;
                    }
                }
            }
        }
    }

    //СТРАНИЦА КАРТОЧКИ ТОВАРА
    .cart{
        &-content{
            &-general{
                .-img{
                    max-width: 460px;
                }
            }
            h1{
                margin-bottom: 40px;
                font-size: 36px;
            }
        }
    }
}




//@media (от 768 до 1023){
@media (max-width: 1023px){
    .wrapper{
        padding: 0 40px;
    }
    .h2-title{
        font-size: 35px;
    }
    .new-block {
        padding: 50px 0;
    }
    .nav-content{
        ul li {
            a{
                font-size: 17px;
                padding: 0 10px;
            }
        }
    }
    .header-content p{
        font-size: 24px;
    }
    .why-we{
        &__content{
            padding-right: 35px;
            h2{
                font-size: 38px;
            }
            img {
                display: none;
            }
        }
        &__img{
            display: flex;
            align-items: center;
            padding-left: 35px;
        }
    }
    .gift-wrapper{
        padding-top: 20px;
        padding-bottom: 20px;
        .-text{
            p{
                font-size: 22px;
            }
            button{
                text-transform: uppercase;
                font-size: 15px;
            }
        }
    }
    .dvizheniye-content{
        &__elem{
            font-size: 18px;
            max-width: 280px;
        }
    }
    .luchshe{
        background-position: 50% 50%;
        background-size: cover;
        &-content{
            grid-template-columns: repeat(2,1fr);
            grid-column-gap: 40px;
            &__elem{
                min-height: auto;
                padding: 75px 0;
                span{
                    padding: 0 20px;
                }
            }
        }
    }    
//    СТРАНИЦА О КОМПАНИИ
    .aboutus{
        &-h2-title {
            font-size: 28px;
        }
        &-header-content-wrapper .-title h1 {
            font-size: 68px;
        }
    //
        &-vipusk{
            .-title {
                margin-bottom: 60px;
            }
            &-content{
                &__elem{
                    width: 50%;
                    .-text{
                        padding: 0 20px 30px 30px;
                        p{

                        }
                    }
                }
            }
        }
    //
        &-strategiya{
            padding: 55px 65px;
            &-content{
                padding: 30px 55px;
                h2{
                    margin-bottom: 15px;
                }
                p{
                    font-size: 17px;
                    color: #000;
                }
            }
        }
    //
        &-personal-content{
            grid-template-columns: repeat(2, 1fr);
            grid-row-gap: 15px;
        }
    }

//    СТРАНИЦА ЗАЯВКИ
    .zayavka-content{
        .-form {
            .zayavka-label{
                max-width: 285px;
            }
            .form-politica{
                justify-content: center;
            }
        }
    }

//СТРАНИЦА КОНТАКТЫ
    .contacts{
        .-title{
            text-align: center;
        }
        &-content{
            grid-template-columns: repeat(2, 1fr);
            grid-row-gap: 30px;
        }
    }

//СТРАНИЦА СЕРТИФИКАТЫ
    .sertifikaty{
        .-title h1{
            font-size: 28px;
        }
        &-content{
            grid-template-columns: repeat(2,1fr);
        }
    }

//СТРАНИЦА ПРОДУКТОВ
    .products{
        .-title{
            margin-top: 70px;
            margin-bottom: 70px;
            h1{
                font-size: 35px;
            }
        }
        &-content{
            grid-template-columns: repeat(2,1fr);
            &__elem{
                .-text{
                    p{
                        font-size: 17px;
                    }
                }
            }
        }
    }

    //СТРАНИЦА КАРТОЧКИ ТОВАРА
    .cart{
        &-content{
            &-general{
                margin-bottom: 65px;
                .-img{
                    max-width: 350px;
                }
                .-text{
                    margin-left: 40px;
                }
            }
            h1{
                margin-bottom: 40px;
                font-size: 32px;
            }
            strong{
                font-size: 18px;
            }
            ul{
                margin-top: 15px;
                font-size: 18px;
                li{
                    font-size: 18px;
                    margin-left: 20px;
                    &:first-child{
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}



//@media (от 768 до 899){
@media (max-width: 899px){
    .feedback-content{
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        &__map {
            order: 2;
            height: 400px;
            width: 100%;
            margin-top: 50px;
        }
        &__form{
            order: 1;
            max-width: 350px;
            padding: 45px 30px;
            .-form__title p{
                font-size: 23px;
            }
        }
        .feedback-content__form .form .-form__content label span{
            font-size: 16px;
        }

    }
}


//@media (от 0 до 767){
@media (max-width: 767px){
    .wrapper{
        padding: 0 15px;
    }
    .h2-title{
        font-size: 30px;
    }
    .new-block{
        padding: 40px 0;
    }
    .auto-padding{
        padding-top: 160px;
        padding-bottom: 70px;
    }
    //
    nav {
        padding: 10px 0;
    }
    .nav{
        &-logo{
            order: 1;
        }
        &-contacts{
            margin-top: 15px;
            order: 3;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            text-align: center;
            p{
                margin-right: 0;
            }
            p:last-child{
                margin-bottom: 0;
            }
        }
        &-content{
            order: 5;
            display: none;
        }
        &-burger-btn{
            display: block;
            cursor: pointer;
            order: 2;
            background: url(../img/header/burger.png);
            background-repeat: no-repeat;
            background-size: contain;
            width: 45px;
            height: 45px;
            margin: 0;
        }

    }
    .header-content{
        text-align: center;
        &-wrapper{
            background: url(../img/header/phone-bg.jpg) center center/cover no-repeat;
        }
        h1 {
            margin-top: 10px;
            font-size: 40px;
            text-shadow: none;
        }
        p{
            font-size: 20px;
            text-shadow: none;
            line-height: 128%;
        }
    }
    //
    .why-we{
        flex-direction: column;
        &__content{
            width: 100%;
            order: 2;
            padding: 0;
            border: none;
            margin-top: 35px;
            h2{
                text-align: center;
                font-size: 32px;
            }
        }
        &__img{
            width: 100%;
            order: 1;
            padding: 0;
        }
    }
    //
    .gift-wrapper{
        flex-direction: column;
        padding-bottom: 50px;
        margin-bottom: 20px;
        .-img{
            order: 1;
        }
        .-text{
            order: 2;
        }
    }
    //
    .dvizheniye-content {
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-top: 80px;
        &__elem{
            max-width: none;
            width: 100%;
            text-align: center;
            p{
                transform: translateX(-50%) translateY(50%);
                left: 50%;
            }
            &:nth-child(3n){
                margin-bottom: 0;
            }
        }
        & > div{
            margin-bottom: 80px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    //
    .luchshe-content{
        grid-template-columns: repeat(1,1fr);
        grid-column-gap: 0;
    }
    //
    .feedback-content{
        &__form{
            padding: 40px 20px;
            width: 100%;
            .form{
                .-form__title p{
                    font-size: 20px;
                }
            }
        }
    }
    //
    .footer{
        padding: 60px 0 40px;
        &-content{
            grid-template-columns: repeat(1,1fr);
            grid-row-gap: 40px;
            &__elem{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                h3{
                    margin-bottom: 20px;
                }
            }
        }

    }
    //
    .politica {
        padding: 10px 20px;
    }
//    fancy-box
    .fancybox-slide{
        padding: 0;
    }
    .fancybox-content{
        width: 100%;
        padding: 0;
    }

    //    СТРАНИЦА О КОМПАНИИ
    .strategiya .wrapper{
        padding: 0;
    }
    .aboutus{
        &-header-content-wrapper{
            .-title {

                h1{
                    font-size: 40px;
                    font-weight: bold;
                    letter-spacing: .11em;
                }
            }
        }
    //
        &-info-content{
            .-descr{
                padding: 35px 35px 40px 35px;
                h2{
                    margin-bottom: 17px;
                }
                p{
                    color: rgba(0,0,0,.75);
                }
            }
        }
    //
        &-vipusk-content__elem{
            width: 100%;
            margin-bottom: 15px;
            padding: 0;
            .-text{
                padding: 0 20px 30px 20px;
                p{
                    line-height: 148%;
                }
            }
        }
    //
        &-strategiya{
            padding: 25px 15px;
            &-content{
                padding: 25px 20px;
                h2{
                    line-height: 120%;
                    letter-spacing: .03em;
                }
                p{
                    line-height: 142%;
                }
            }
        }
    //
        &-personal-content{
            grid-template-columns: repeat(1,1fr);
            grid-column-gap: 0;
            //grid-row-gap: 15px;
        }

    }

    //    СТРАНИЦА ЗАЯВКИ
    .zayavka{
        &-content{
            .-title{
                margin-bottom: 54px;
                h1{
                    font-size: 30px;
                }
                p{
                    font-size: 18px;
                }
            }
            .-form{
                justify-content: center;
                padding: 30px 25px;
                padding-bottom: 35px;
                .zayavka-label{
                    width: 100%;
                    max-width: 285px;
                    span{
                        font-size: 16px;
                    }
                    input[type=file]{
                        width: 100%;
                    }
                }
                .form-politica{
                    width: 100%;
                }
                button{
                    margin-top: 45px;
                }
            }
        }
    }

    //СТРАНИЦА КОНТАКТЫ
    .contacts{
        padding-top: 90px;
        &-wrapper{
            padding-bottom: 50px;
        }
        .-title{
            margin-bottom: 50px;
            h1{
                font-size: 32px;
            }
        }
        &-content{
            grid-template-columns: repeat(1, 1fr);
        }
    }

    //СТРАНИЦА СЕРТИФИКАТЫ
    .sertifikaty{
        .-title {
            margin-bottom: 40px;
            h1{
                text-align: center;
                font-size: 24px;
            }
        }
        &-content{
            grid-template-columns: repeat(1,1fr);
            grid-gap: 15px;
        }
    }

    //СТРАНИЦА ПРОДУКТОВ
    .products{
        .-title h1{
            font-size: 32px;
        }
        &-content{
            grid-template-columns: repeat(1,1fr);
            grid-gap: 25px 0;
        }
    }

    //СТРАНИЦА КАРТОЧКИ ТОВАРА
    .cart{
        &-content{
            &-general{
                margin-bottom: 40px;
                flex-wrap: wrap;
                .-img{
                    padding: 10px;
                    max-width: 400px;
                }
                .-text{
                    margin-left: 0;
                    margin-top: 40px;
                }
            }
            h1{
                margin-bottom: 30px;
            }
            ul{
                line-height: 132%;
            }
        }
    }

}